@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500;600;700;800;900&family=Hind:wght@300;400;500;600;700&family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Kumbh+Sans:wght@400;700&family=Lobster&family=Montserrat:wght@400;600&family=Nunito:wght@300;400;600;700&family=Pacifico&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Rubik:ital,wght@0,400;0,500;0,600;0,700;1,700&family=Satisfy&family=Work+Sans:wght@200;300;400;500;600;700;800&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: rgb(234, 237, 237);
}


.header {
  height: 70px;
  display: flex;
  align-items: center;
  background-color: #131921;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 150;
}

.header__logo {
  width: 100px;
  object-fit: contain;
  margin: 0 20px;
  margin-top: 18px;
}

.header__search {
  display: flex;
  align-items: center;
  flex: 1 1;
}

.header__searchInput {
  height: 40px;
  padding: 10px;
  border: 0;
  width: 100%;
  outline: 0;
}

.header__searchIcon {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ff9900;
  padding: 5px 7px;
  color: #fff;
  cursor: pointer;
  height: 40px;
  width: 40px;
}

.header__nav {
  color: #fff;
  display: flex;
  align-items: center;
}

.header__option {
  display: flex;
  align-items: center;
  margin-left: 5px;
  margin-right: 5px;
  padding: 5px;
}

.header__btn {
  margin: 0 7px;
  outline: 0;
  border: 0;
  background: #ff9900;

  color: #fff;
  padding: 5px 15px;
  font-size: 15px;
  font-weight: 400;
  border-radius: 6px;
  cursor: pointer;
}

.header__optionLineOne {
  font-size: 15px;
}

.header__optionLineTwo {
  font-size: 13px;
  font-weight: 700;
}

.header__optionBasket {
  display: flex;
  align-items: center;
}

.uil-shopping-cart-alt {
  font-size: 25px;
  cursor: pointer;
  color: #fff;
}

.header__basketCount {
  margin-left: 10px;
  margin-right: 10px;
  color: #fff;
}

@media (max-width: 1200px) {
  .header__search {
    flex: 0.5 1;
  }
  .header__nav {
    flex: 0.4 1;
    justify-content: flex-end;
  }
}

@media (max-width: 850px) {
  .header__search {
    flex: 2 1;
  }
  .header__option {
    display: none;
  }
}

.home__row {
  padding: 0 10%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 20px;
  gap: 20px;
}

.img__container {
  overflow: hidden;
  margin-bottom: 20px;
}

.img__container > div {
  cursor: pointer;
}

.home__img {
  width: 100%;
  z-index: -1;
  margin-bottom: -150px;
  -webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
          mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  height: 100%;
}

.home__title {
  padding: 0 10%;
  margin: 2% 0;
  position: relative;
}

.home__title::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 17%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 50px;
  height: 2px;
  background: #ff9900;
}

@media (max-width: 700px) {
  .home__img {
    width: 150%;
  }
  .home__title {
    margin: 0 0;
  }
  .img__container {
    margin-bottom: 5px;
  }
}

.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  padding: 20px 10px;
  margin: 10px 0;
  background-color: white;
  box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2), 0px 0px 50px rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  transition: all 0.3s;
  z-index: 1;
}

.product:hover {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}

.product__rating {
  display: flex;
  justify-content: center;
}

.product > img {
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
  margin-top: 15px;
}

.product__price {
  margin-top: 5px;
}

.product__info {
  margin-bottom: 15px;
  width: 80%;
  text-align: center;
}

p:first-child {
  text-align: center;
}

.product__btn {
  outline: 0;
  background: #ff9900;
  border: 0;

  color: #fff;
  padding: 10px 20px;
  font-size: 20px;
  font-weight: 600;
  border-radius: 6px;
  cursor: pointer;
  transition: 0.3s;
}

.product__btn:hover {
  letter-spacing: 1.2px;
}

@media (max-width: 700px) {
  .product {
    margin-top: 60px;
  }
}

.top__container {
  margin: 50px 0;
  padding: 2% 10%;
  background: #fff;
}

.top__container h1 {
  font-size: 25px;
  margin-bottom: 20px;
}

.top__img img {
  width: auto;
  height: auto;
  max-width: 270px;
  max-height: 200px;
  vertical-align: middle;
  border-radius: 6px;
}

@media (max-width: 700px) {
  .top__container {
    display: none;
  }
}

.box__container {
  margin-top: -150px;
  position: relative;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0 2%;
  z-index: 100;
}

.box {
  height: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background: #fff;
  padding: 10px;
  margin: 10px;
  border-radius: 6px;
}

@media (max-width: 700px) {
  .box__container {
    flex-direction: column;
    margin-top: 0;
  }
}

.box__title {
  font-size: 16px;
  margin-bottom: 10px;
}

.img__box {
  overflow: hidden;
}

.box img {
  width: 300px;
  transition: 0.5s;
}

.box img:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.subtotal {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 15px;
  gap: 15px;
  width: 300px;
  height: 150px;
  padding: 20px;
  margin-left: 10px;
  background: #f9f9f9;

  border-radius: 4px;
}

@media (max-width: 700px) {
  .subtotal {
    margin: 0 auto;
    width: 100%;
  }
}

.subtotal__gift {
  display: flex;
  align-items: center;
}

.subtotal__gift > input {
  margin-right: 5px;
}

.subtotal__btn {
  width: 100%;
  border: 0;
  outline: 0;
  padding: 8px 16px;
  margin-top: 5px;
  border-radius: 11px;
  cursor: pointer;
  background: #ff9000;
  transition: all 0.5s ease;
  color: #fff;
}

.subtotal__btn:hover {
  background: #fff;
  color: #000;
  letter-spacing: 1.1px;
}

.checkout {
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
  background: #fff;
  height: -webkit-max-content;
  height: max-content;
}

@media (max-width: 700px) {
  .checkout {
    flex-direction: column;
  }
}

.checkout__add {
  width: 100%;
  height: 42vh;
  border-radius: 4px;
  margin-bottom: 10px;
}

.checkout__title {
  margin-right: 10px;
  padding: 10px;
  border-bottom: 1px solid lightgray;
}

.checkoutProduct {
  display: flex;
  margin-top: 30px;
  margin-bottom: 30px;
  padding: 30px;
  border-bottom: 1px solid #555;
}

.checkoutProduct:last-child {
  border-bottom: 0;
}

.checkoutProduct__info {
  padding-left: 30px;
}

.checkoutProduct__title {
  font-size: 15px !important;
  font-weight: 600 !important;
  max-width: 500px;
  margin-bottom: 10px;
}

.checkoutProduct__info > button {
  outline: 0;
  border: 0;
  background: #ff9900;

  margin-top: 10px;

  color: #fff;
  padding: 10px 20px;
  font-size: 17px;
  font-weight: 500;
  border-radius: 6px;
  cursor: pointer;
}

.checkoutProduct__image {
  object-fit: contain;
  width: 180px;
  height: 180px;
}

.checkoutProduct__rating {
  display: flex;
}

.checkoutProduct__title {
  font-size: 17px;
  font-weight: 800;
}

.footer__container {
  background: #0f1111;
  padding: 10px 0;
}

.footer__wrap {
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  color: #fff;
}

.footer__wrap img {
  width: 150px;
}

.footer__social {
  list-style: none;
  display: flex;
  align-items: center;
}

.footer__social a {
  color: #fff;
  padding: 0 15px;
}

.footer__social a i {
  font-size: 25px;
}

.login {
  background-color: white;
  border-radius: 6px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 80px;
}

.login__logo {
  margin-top: 20px;
  margin-bottom: 20px;
  object-fit: contain;
  width: 100px;
  margin-right: auto;
  margin-left: auto;
}

.login__container {
  width: 300px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 1px solid lightgray;
  padding: 20px;
}

.login__container > h1 {
  font-weight: 600;
  margin-bottom: 10px;
}

.forget {
  text-align: right;
  font-size: 12px;
  font-weight: 500;
  cursor: pointer;
  margin-bottom: 10px;
}

.login__container > form > input {
  height: 20px;
  margin-bottom: 10px;
  background-color: white;
  width: 98%;

  outline: 0;
  border: none;

  padding: 10px;
  border-bottom: 1px solid #000;
  transition: 0.3s;
}

.login__container > form > input:focus {
  border-color: #ff9000;
}

.login__container > p {
  text-align: center;
  margin-top: 15px;
  margin-bottom: 10px;
  font-size: 15px;
  opacity: 0.85;
  position: relative;
}
.login__container > p::after {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  height: 1px;
  width: 25%;
  background: #000;
}

.login__container > p::before {
  content: '';
  position: absolute;
  right: 0;
  top: 50%;
  height: 1px;
  width: 25%;
  background: #000;
}

.login__signInButton {
  background: #f0c14b;
  border-radius: 4px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  font-size: 15px;
}

.login__registerButton {
  border-radius: 4px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: darkgray;
}

.headerlog {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background: #000;
  color: #fff;
}

.headerlog__optionLineOne {
  font-size: 20px;
  font-weight: 600;
}

